import { NextRequest, NextResponse } from 'next/server'

export async function POST(request: NextRequest) {
  try {
    const { name, email, phone, conversation, userId, timestamp } = await request.json()

    // Validate required fields
    if (!name || !email || !phone || !conversation || !timestamp) {
      return NextResponse.json(
        { error: 'Missing required fields: name, email, phone, conversation, and timestamp' },
        { status: 400 }
      )
    }

    const FALLBACK_N8N_WEBHOOK_URL = process.env.FALLBACK_N8N_WEBHOOK_URL

    console.log('FALLBACK_N8N_WEBHOOK_URL:', FALLBACK_N8N_WEBHOOK_URL)

    if (!FALLBACK_N8N_WEBHOOK_URL) {
      console.error('FALLBACK_N8N_WEBHOOK_URL environment variable not set')
      return NextResponse.json(
        { error: 'Fallback webhook URL not configured' },
        { status: 500 }
      )
    }

    // Forward the contact form data to n8n webhook
    const response = await fetch(FALLBACK_N8N_WEBHOOK_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name,
        email,
        phone,
        conversation,
        userId: userId || 'anonymous',
        timestamp,
      }),
    })

    if (!response.ok) {
      console.error('Fallback n8n webhook error:', response.status, response.statusText)
      return NextResponse.json(
        { error: 'Failed to send contact form data to webhook' },
        { status: 500 }
      )
    }

    const webhookResponse = await response.json()

    return NextResponse.json({
      success: true,
      response: webhookResponse
    })

  } catch (error) {
    console.error('Fallback webhook API error:', error)
    return NextResponse.json(
      { error: 'Internal server error' },
      { status: 500 }
    )
  }
}
